export default {
	data() {
		return {
            flag:false,
            many:'0',
            tabCurrentIndex:0,
            dater:'',
            ter:'',
            name:'',
            phone:'',
            title:'',
            delwen:'',
            delewen:'',
            ebti:'',
            navList: [
            	{
            		state: 0,
            		text: '待付款'
            	
            	},
            	{
            		state: 1,
            		text: '待确认'
            		
            	},
            	{
            		state: 2,
            		text: '待审核'
            		
            	},
                {
                    state: 3,
                    text:  '已完成'
                }
            ]
		};
    },
    onLoad(options) {
        var  that  = this
        uni.request({
            url: this.url + 'saleall/101',
            method:'GET',
            header:{
                Authorization: 'JWT'+' '+this.global_.token
            },
            success(res) {
                var dater = res.data.data
                that.dater = dater.reverse()
            }
        })
        uni.request({
            url:this.url + 'ordernum/2',
            method:'GET',
            header:{     
                Authorization: 'JWT'+' '+this.global_.token
            },
            success(res) {
                that.many = res.data.data
            }
        })
    },
    methods:{
        obtainOrderNum:function(e) {
            var that = this
            that.ebit = e
        },
        getPhoneValue:function(e){
        	this.phone=e.detail.value
        },
        tabClick:function (index) {
            var that = this
            if (this.tabCurrentIndex === index) {
                return false
            }else {
                that.tabCurrentIndex =index
            }
            if(this.tabCurrentIndex === 0){
                var that = this
                uni.request({
                    url: this.url + 'saleall/101',
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var dater = res.data.data
                        that.dater = dater.reverse()
                    }
                })
            }
            if(this.tabCurrentIndex === 1) {
                var that  = this
                uni.request({
                    url:this.url + 'saleall/102',
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var ter = res.data.data
                        that.ter = ter.reverse()//倒序
                    }
                })
            }
            if(this.tabCurrentIndex === 2) {
                var that  = this
                uni.request({
                    url:this.url + 'saleall/103',
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var delwen = res.data.data
                        that.delwen = delwen.reverse()
                    }
                })
            }
            if(this.tabCurrentIndex === 3) {
                var that  = this
                uni.request({
                    url:this.url + 'saleall/104',
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var delewen = res.data.data
                        that.delewen = delewen.reverse()
                    }
                })
            }
        },
        btn:function(item){
            console.log(item)
            var that = this   
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    console.log(res)
                    var order = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:'../salepay/salepay?aser=' + order
                    })
                }
            })
        },
        btn1:function(item){
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    var ordear = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:'../saleconfirm/saleconfirm?mvp=' + ordear
                    })
                }
            })
        },
        btn2:function(item){
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                   var nuso = JSON.stringify(res.data.data)
                   uni.navigateTo({
                       url:'../saleaudit/saleaudit?suxang=' + nuso
                   })
                }
            })
        },
        btn3:function(item){
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                   var ksa = JSON.stringify(res.data.data)
                   uni.navigateTo({
                       url:'../salecompleted/salecompleted?cshug=' + ksa
                   })
                }
            })
        },
        butto:function () {
            var that = this 
            that.flag=true
            that.arr = []
        },
        deer:function() {
            var that = this
            uni.request({
                url:this.url + 'orderappeal/',
                method:'POST',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    info:that.phone,
                    order_num:that.ebit
                },
                success(res) {
                    if(res.statusCode == 200) {
                        that.flag = false
                        uni.showToast({
                            title:'已提交您的'
                        })
                    }else if (res.statusCode == 400) {
                        uni.showToast({
                            title:'该订单不可申诉',
                            icon:'none'
                        })
                        that.flag = false
                    }
                }
            })
        }
    }
}
